import request from '@/utils/request'

export default {
  // 获取讲师分页信息
  getTeacherList(page, pageSize) {
    return request({
      url: `/eduService/teacherFront/teacherList/${page}/${pageSize}`,
      method: 'get'
    })
  },
  // 讲师详情
  getTeacherDetail(teacherId) {
    return request({
      url: `/eduService/teacherFront/teacherDetail/${teacherId}`,
      method: 'get'
    })
  }
}